import React from 'react';
import PropTypes from 'prop-types';
import { useStaticQuery, graphql, Link } from 'gatsby';
import Url from 'url';
import { OutboundLink } from 'gatsby-plugin-google-analytics';

import { Layout, PostCard, HeaderIndexAlt, Tag } from '../components/common';
import { StickyNavContainer } from '../components/common/effects';
import { WebsiteMeta } from '../components/common/meta';
import ArrowRightIcon from '../components/common/icons/arrow-right';
