      primary_author: {
        name: 'COVID Translate Team',
        slug: 'SLUG',
      },
      authors: [{ name: 'COVID Translate Team', slug: 'SLUG' }],
      primary_tag: { name: 'COVID-19 Resources' },
      published_at: publishedAt3.toISOString(),
      published_at_pretty: publishedAt3.toDateString(),
